.forum-buttons {
    display: flex;
    justify-content: center; 
    align-items: center; 
    margin-top: 20px; 
  }
  
  .post-button,
  .qa-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 20px;
    margin-right: 10px;
  }
  
  .qa-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .post-button:hover,
  .qa-button:hover {
    background-color: #0056b3;
  }
  
  .post-button:focus,
  .qa-button:focus {
    outline: none;
  }