.container {
    width: 80%;
    margin: 0 auto;
    background-color: #C3C3E5; 
    border-radius: 50px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden; 
    margin: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: flex-start;
  }
  
  .column {
    width: 20%;
    float: left;
    padding-right: 20px;
    padding-left: 20px; 
    position: relative;
  }
  
  .column:not(:last-child)::after {
    content: '';
    position: absolute;
    top: 10%;
    bottom: 10%;
    right: 0;
    width: 2px;
    background-color: #999;
  }
  
  .column:last-child {
    border-right: none; 
  }
  
  input[type="radio"] {
    display: none;
  }
  
  .column label {
    position: relative;
    display: block;
    cursor: pointer;
    padding: 10px 20px;
    border-radius: 30px;
    background-color: #f2f2f2;
    color: #666;
    margin-bottom: 10px;
    transition: background-color 0.3s ease;
  }
  
  .column label::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
  }
  
  input[type="radio"]:checked + label {
    color: #66CCFC;
    color: #fff;
  }
  
  input[type="radio"]:checked + label::before {
    color: #fff;
  }
  
  .filter-title {
    font-weight: bold;
    margin-bottom: 10px;
    color: #fff;
  }
  
  .column select {
    width: 100%;
    padding: 8px;
    border-radius: 5px;
    border: 1px solid #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 15px;
    background-color: #66CCFF;
  }
  
  select option:checked {
    background-color: #66CCFF;
    color: #fff;
  }
  
  select option {
    background-color: #fff;
    color: #000;
  }
  
  select option[value="None"] {
    background-color: #fff; 
    color: #000; 
  }