.forum-switch {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .switch-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 20px;
    margin-right: 10px;
  }
  
  .switch-button.active {
    background-color: #0056b3;
  }